import React from 'react';
import { AboutVDE } from './about-vde/about-vde.component';
import { OurValues } from './our-values/our-values.component';
import { Sharing } from './sharing/sharing.component';
import { CSSTransition } from 'react-transition-group';
import { SectionsContainer, Section, Header, Footer } from 'react-fullpage';
import { Header as MobileHeader } from '../../ui/mobile-header/header.component';
import { Footer as FooterCmp } from '../../ui/footer/footer.component';
import { useStickyNav } from '../../hooks/use-sticky-nav/use-sticky-nav.hook';
import ReactFullpage from '@fullpage/react-fullpage';

const Animate = ({ transitionIn, transition = 'fade', ...props }) => (
  <CSSTransition
    in={transitionIn}
    unmountOnExit
    timeout={2000}
    classNames={transition}
  >
    <div className={transition} {...props} />
  </CSSTransition>
);

export const AboutContainer = () => {
  const options = {
    sectionClassName: 'section',
    anchors: ['about-vde', 'our-values', 'sharing'],
    scrollBar: false,
    navigation: false,
    verticalAlign: false,
    arrowNavigation: false,
    scrollOverflow: false,
  };

  const [isScrollingTop, setScrollingTop] = React.useState(false);
  const [lastScroll, setLastScroll] = React.useState(0);

  const onScroll = (states) => {
    setScrollingTop(lastScroll > states.activeSection);
    setLastScroll(states.activeSection);
  };

  return (
    <>
      <ReactFullpage
        scrollOverflow={true}
        render={({ state, fullpageApi }) => {
          return (
            <div id="fullpage-wrapper">
              <div className="section">
                <AboutVDE />
              </div>
              <div className="section">
                <OurValues />
              </div>
              <div className="section">
                <h3>Section 3</h3>
                <button onClick={() => fullpageApi.moveTo(1, 0)}>
                  Move top
                </button>
              </div>
            </div>
          );
        }}
      />
    </>
  );
};
